#drum-machine {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    min-height: 60%;
}
#display {
    width: 100%;
    align-self: center;
    text-align: center;
}
.drum-pad {
    margin: 20px;
    background-color: grey;
    min-width: 25%;
    min-height: 25%;
    border-radius: 13px;
    box-shadow: 3px 3px 5px black;
}
.drum-pad:hover {
    background-color: purple;
}
.audio {
    min-width: 100%;
    min-height: 100%;
    background-color: blue;
}